@charset "utf-8";

*{
  margin:0;
  padding:0;
  outline: none;
  border-style: none;
  vertical-align: baseline;
  box-sizing: border-box;
}

html, body {
    height: 100%;
    margin: 0;
}

/* The container should take up the full height and use flexbox for layout */
.container {
    display: flex;
    flex-direction: column;
    min-height: 100vh;
}

/* Main content should take up all available space */
main {
    flex: 1;
}

header {
    display: flex;
    justify-content: space-between; /* Ensure space between the h1 and nav */
    align-items: center; /* Align items vertically center */
    background-color: #00b100;
    color: white;
    padding: 10px 300px;
}
  
nav ul {
    list-style: none;
    display: flex;
    gap: 20px; /* Space between nav items */
}
  
nav ul li a {
    color: white;
    text-decoration: none;
    font-size: 20px;
}


.banner {
    padding: 40px 300px;
    text-align: center;
    background-color: #f9f9f9;
    margin-bottom: 20px;
}

.banner h2 {
    margin-bottom: 10px;
}

.banner p {
    margin-bottom: 20px;
    line-height: 1.6;
   
}

.banner .primary {
    background-color: blue;
    color: white;
    border: none;
    padding: 10px 20px;
    border: 2px solid blue;
    margin: 0 10px;
}

.banner .secondary {
    background: none;
    color: blue;
    border: 2px solid blue;
    padding: 10px 20px;
    margin: 0 10px;
}

.gallery {
    display: flex;
    justify-content: center;
    gap: 20px;
    padding-bottom: 40px;
    padding-top: 60px;
    background-color: #fff;
}

.gallery img {
    width: 150px;
    height: 220px;
    object-fit: cover;
    border-radius: 5px;
}

footer {
    background-color: #ccc;
    color: white;
    text-align: center;
    padding: 20px;
}

button {
    cursor: pointer;
}

